import React from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import camps from '../camps.json';

const CampCard = styled(motion.div)`
  background: var(--glass-bg);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 15px;
  border: var(--glass-border);
  padding: 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    border-color: rgba(0, 245, 160, 0.5);
  }
`;

const CardBody = styled.div`
  flex-grow: 1;
`;

const cardVariants = {
  offscreen: {
    y: 50,
    opacity: 0
  },
  onscreen: {
    y: 0,
    opacity: 1,
    transition: {
      type: "spring",
      bounce: 0.4,
      duration: 0.8
    }
  }
};

function CampList({ onCampSelect }) {
  return (
    <section id="camps">
      <Container>
        <Row className="text-center mb-5">
          <Col>
            <h2 className="gradient-text">Our Core Programs</h2>
            <p className="lead text-white-50">       .</p>
          </Col>
        </Row>
        <Row>
          {camps.map((camp, index) => (
            <Col md={4} key={camp.id} className="mb-4">
              <CampCard
                initial="offscreen"
                whileInView="onscreen"
                viewport={{ once: true, amount: 0.3 }}
                variants={cardVariants}
                custom={index}
              >
                <CardBody>
                  <h4>{camp.title}</h4>
                  <p className="text-muted mb-2">{camp.period}</p>
                  <p>
                    <strong>:</strong> {camp.target}<br/>
                    <span className="text-white-50">{camp.description}</span>
                  </p>
                </CardBody>
                <Button className="gradient-btn mt-auto" onClick={() => onCampSelect(camp)}>
                   
                </Button>
              </CampCard>
            </Col>
          ))}
        </Row>
      </Container>
    </section>
  );
}

export default CampList;