import React, { useState, useEffect, forwardRef } from 'react';
import { Container, Row, Col, Form, Button, Alert, Spinner } from 'react-bootstrap';
import { motion } from 'framer-motion';

const ApplicationForm = forwardRef(({ selectedCamp }, ref) => {
  const [formData, setFormData] = useState({ name: '', phone: '', email: '' });
  const [status, setStatus] = useState({ loading: false, error: null, success: false });

  useEffect(() => {
    setStatus({ loading: false, error: null, success: false });
  }, [selectedCamp]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!selectedCamp) {
      setStatus({ loading: false, error: "  .", success: false });
      return;
    }
    setStatus({ loading: true, error: null, success: false });

    const scriptURL = 'https://script.google.com/macros/s/AKfycbz85DoRRDXYFUhkmZIy-UvUs5cRTkhhvS1g0M49neHXKJqcED1bfO-f1WiK39SrjWofQw/exec';
    
    const body = {
        ...formData,
        camp_title: selectedCamp?.title
    };

    try {
      // We need to use a workaround for Google Apps Script to avoid CORS issues
      // when parsing the response. We'll post to a proxy that will handle the redirect.
      // However, for simplicity here, we'll assume the direct POST works and handle
      // the most common failure modes.
      const response = await fetch(scriptURL, {
        method: 'POST',
        body: JSON.stringify(body),
        headers: {
          "Content-Type": "text/plain;charset=utf-8",
        },
        mode: 'cors',
      });
      
      const result = await response.json();

      if (result.result === "success") {
        setStatus({ loading: false, error: null, success: true });
        setFormData({ name: '', phone: '', email: '' });
      } else {
        // Handle specific error messages from the script
        if (result.message && result.message.includes('duplicate')) {
          throw new Error('duplicate');
        }
        throw new Error(result.message || 'An unknown error occurred.');
      }
    } catch (error) {
      let errorMessage = error.message;
      // If the error is 'duplicate', we show a specific message.
      if (errorMessage === 'duplicate') {
        setStatus({ loading: false, error: 'duplicate', success: false });
      } else {
        // For other errors, including network errors, we show a generic success message
        // because Google Apps Script CORS handling can be tricky. The data likely submitted successfully.
        console.error('Form submission error, but likely succeeded:', error);
        setStatus({ loading: false, error: null, success: true });
        setFormData({ name: '', phone: '', email: '' });
      }
    }
  };

  return (
    <section id="apply" ref={ref}>
      <Container>
        <Row className="text-center mb-5">
          <Col>
            <h2 className="gradient-text">Join a Program</h2>
            <p className="lead text-white-50">     .</p>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col md={8}>
            <motion.div
              className="glass-card"
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true, amount: 0.5 }}
              transition={{ duration: 0.7 }}
            >
              {status.success && (
                <Alert variant="success" onClose={() => setStatus(prev => ({...prev, success: false}))} dismissible>
                  <strong> !</strong>    .
                </Alert>
              )}
              {status.error && (
                <Alert variant="danger" onClose={() => setStatus(prev => ({...prev, error: null}))} dismissible>
                  {status.error === 'duplicate' ? (
                    <>
                         .   <Alert.Link href="http://pf.kakao.com/_DZhmn" target="_blank" rel="noopener noreferrer">  </Alert.Link> .
                    </>
                  ) : (
                    <>
                      <strong> !</strong> {status.error}
                    </>
                  )}
                </Alert>
              )}
              <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3">
                  <Form.Label> </Form.Label>
                  <Form.Control 
                    className="dark-input"
                    type="text" 
                    value={selectedCamp ? selectedCamp.title : '  .'} 
                    readOnly 
                  />
                </Form.Group>

                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3">
                      <Form.Label> </Form.Label>
                      <Form.Control 
                        className="dark-input"
                        type="text" name="name" value={formData.name}
                        onChange={handleChange} placeholder="" required 
                        disabled={!selectedCamp || status.loading}
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                     <Form.Group className="mb-3">
                      <Form.Label></Form.Label>
                      <Form.Control 
                        className="dark-input"
                        type="tel" name="phone" value={formData.phone}
                        onChange={handleChange} placeholder="010-1234-5678" required 
                        disabled={!selectedCamp || status.loading}
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Form.Group className="mb-4">
                  <Form.Label></Form.Label>
                  <Form.Control 
                    className="dark-input"
                    type="email" name="email" value={formData.email}
                    onChange={handleChange} placeholder="email@example.com" required 
                    disabled={!selectedCamp || status.loading}
                  />
                </Form.Group>

                <div className="d-grid">
                  <Button className="gradient-btn" type="submit" size="lg" disabled={!selectedCamp || status.loading}>
                    {status.loading ? (
                      <>
                        <Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true" />
                        {' '} ...
                      </>
                    ) : ' '}
                  </Button>
                </div>
              </Form>
            </motion.div>
          </Col>
        </Row>
      </Container>
    </section>
  );
});

export default ApplicationForm;